import React from "react";
import {
  ListItem,
  ListItemSecondaryAction,
  Typography,
} from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import Checkbox from "@material-ui/core/Checkbox";
import { List, ListItemText, Button } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { BottomNavigation, BottomNavigationAction } from "@material-ui/core";
import DoneAllSharpIcon from "@material-ui/icons/DoneAllSharp";
import LocalLaundryServiceSharpIcon from "@material-ui/icons/LocalLaundryServiceSharp";
import EventNoteSharpIcon from "@material-ui/icons/EventNoteSharp";
import ListAltSharpIcon from "@material-ui/icons/ListAltSharp";

class TodoList extends React.Component {
  render() {
    const todoList = this.props.todoList;
    const checkList = this.props.checkList;

    const todos = todoList.map((todo, index) => {
      return (
        <ListItem dense key={index} button>
          <IconButton>
            <Checkbox
              edge="start"
              checked={checkList[index]}
              onClick={() => this.props.handleCheck(todo)}
              style={{
                color: "rgba(175, 47, 47, 0.75)",
                width: 10,
                height: 10,
              }}
            />
          </IconButton>
          <ListItemText id="standard-full-width" fullWidth>
            {todo}
          </ListItemText>
          <ListItemSecondaryAction>
            <IconButton
              edge="end"
              aria-label="delete"
              onClick={() => this.props.deleteItem(todo)}
              disabled={checkList[index]}
            >
              <DeleteIcon fontSize="small" />
            </IconButton>
          </ListItemSecondaryAction>
        </ListItem>
      );
    });
    const itemMsg =
      checkList.filter((item) => item === false).length > 1
        ? checkList.filter((item) => item === false).length + " items left"
        : checkList.filter((item) => item === false).length + " item left";

    todos.push(
      <ListItem dense key={todoList.length}>
        <Grid container style={{ alignItems: "flex-end" }}>
          <Grid item xs={3}>
            <Typography
              variant="subtitle2"
              align="left"
              style={{ color: "rgba(175, 47, 47, 0.75)", marginBottom: 10 }}
            >
              {itemMsg}
            </Typography>
          </Grid>
          <Grid item xs={3}>
            <BottomNavigation
              value={this.props.radioValue}
              onChange={this.props.handleRadioCheck}
              style={{ width: 390, fontSize: "100px" }}
            >
              <BottomNavigationAction
                label="all"
                value="all"
                style={{ color: "rgba(175, 47, 47, 0.75)" }}
                icon={<ListAltSharpIcon />}
              />
              <BottomNavigationAction
                label="active"
                value="active"
                style={{ color: "rgba(175, 47, 47, 0.75)" }}
                icon={<EventNoteSharpIcon />}
              />
              <BottomNavigationAction
                label="completed"
                value="completed"
                style={{ color: "rgba(175, 47, 47, 0.75)" }}
                icon={<DoneAllSharpIcon />}
              />
            </BottomNavigation>
          </Grid>
          <Grid item xs={3}>
            <ListItemSecondaryAction>
              {checkList.includes(true) && (
                <Button
                  onClick={this.props.deleteCheckedAll}
                  style={{
                    color: "rgba(175, 47, 47, 0.75)",
                    fontSize: 15,
                    textTransform: "lowercase",
                  }}
                  startIcon={<LocalLaundryServiceSharpIcon />}
                >
                  clear completed!
                </Button>
              )}
            </ListItemSecondaryAction>
          </Grid>
        </Grid>
      </ListItem>
    );

    return <List>{todos}</List>;
  }
}

export default TodoList;
