import React from "react";

import { Container, Typography } from "@material-ui/core";
import TodoList from "./todoList";
import CssTextField from "./todoInput";

class App extends React.Component {
  constructor(props) {
    super();
    this.state = {
      todo: "",
      todoType: 0,
      shopTodoList: [] || "",
      shopCheckList: [] || "",
      checkAll: false,
      radioValue: "all",
      radioList: [] || "",
      radioCList: [] || "",
    };

    this.handleClick = this.handleClick.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.handleCheck = this.handleCheck.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
    this.selectAll = this.selectAll.bind(this);
    this.handleRadioCheck = this.handleRadioCheck.bind(this);
    this.deleteCheckedAll = this.deleteCheckedAll.bind(this);
  }
  selectAll() {
    let check = this.state.checkAll;

    const checkList = this.state.shopCheckList.slice();
    let cList = checkList.map((todo) => (todo = !check));
    this.setState({
      checkAll: !check,
      shopCheckList: cList,
      radioValue: !check ? "completed" : "active",
    });
  }
  handleInput(e) {
    this.setState({
      todo: e && e.target.value,
    });
  }
  handleChange(e) {
    this.setState({
      todoType: e && e.target.value,
    });
  }
  handleRadioCheck(e, newValue) {
    let target = newValue;
    let todoList = this.state.radioList.slice();
    let checkList = this.state.radioCList.slice();
    if (target !== "all") {
      var res = this.orderList(target, todoList, checkList);
      todoList = res[0];
      checkList = res[1];
    } else {
      todoList = this.state.radioList.slice();
      checkList = this.state.radioCList.slice();
    }
    this.setState({
      radioValue: target,
      shopTodoList: todoList,
      shopCheckList: checkList,
    });
  }
  orderList(target, todoList, checkList) {
    let list = [];
    let cList = [];
    for (const [index, todo] of todoList.entries()) {
      if (target === "active" && !checkList[index]) {
        list.push(todo);
        cList.push(checkList[index]);
      }
      if (target === "completed" && checkList[index]) {
        list.push(todo);
        cList.push(checkList[index]);
      }
    }
    return [list, cList];
  }
  handleKeyDown(e) {
    if (e.key === "Enter") {
      this.handleClick();
    }
  }
  handleClick() {
    let todoList = this.state.shopTodoList.slice();
    if (
      this.state.todo &&
      todoList.filter((item) => item === this.state.todo).length === 0
    ) {
      let checkList = this.state.shopCheckList.slice();
      todoList[todoList.length] = this.state.todo;
      checkList[checkList.length] = false;
      this.setState({
        shopTodoList: todoList,
        todo: "",
        shopCheckList: checkList,
        radioList: todoList,
        radioCList: checkList,
      });
    }
  }
  handleCheck(todo) {
    let todoList = null;
    let checkList = null;
    todoList = this.state.shopTodoList.slice();
    checkList = this.state.shopCheckList.slice();
    let index = todoList.indexOf(todo);
    checkList[index] = !checkList[index];
    this.setState({
      shopCheckList: checkList,
      radioCList: checkList,
    });
  }
  deleteItem(todo) {
    let todoList = this.state.shopTodoList.slice();
    let checkList = this.state.shopCheckList.slice();
    let index = todoList.indexOf(todo);
    todoList.splice(index, 1);
    checkList.splice(index, 1);
    this.setState({
      shopTodoList: todoList,
      shopCheckList: checkList,
      radioList: todoList,
      radioCList: checkList,
    });
  }
  deleteCheckedAll() {
    let todoList = this.state.shopTodoList.slice();
    let checkList = this.state.shopCheckList.slice();
    for (var i = 0; i < todoList.length; i++) {
      if (checkList[i]) {
        todoList.splice(i, 1);
        checkList.splice(i, 1);
        i--;
      }
    }
    this.setState({
      shopTodoList: todoList,
      shopCheckList: checkList,
      radioList: todoList,
      radioCList: checkList,
      checkAll: false,
      radioValue: "all",
    });
  }

  render() {
    return (
      <Container
        style={{
          alignItems: "flex-end",
          direction: "column",
          justify: "center",
          width: 800,
        }}
      >
        <Typography
          variant="h1"
          align="center"
          style={{ color: "rgba(175, 47, 47, 0.75)" }}
        >
          todos
        </Typography>
        <CssTextField
          value={this.state.todo}
          checkAll={this.state.checkAll}
          onChange={this.handleInput}
          onKeyDown={this.handleKeyDown}
          onSelectAll={this.selectAll}
        />
        <TodoList
          todoList={this.state.shopTodoList}
          checkList={this.state.shopCheckList}
          radioValue={this.state.radioValue}
          deleteItem={this.deleteItem}
          handleCheck={this.handleCheck}
          handleRadioCheck={this.handleRadioCheck}
          deleteCheckedAll={this.deleteCheckedAll}
        />
      </Container>
    );
  }
}
export default App;
